%btn {
	display: inline-block;
	padding: 0;
	border: none;
	outline: none;
	background: none;
	appearance: none;
	cursor: pointer;
	-webkit-tap-highlight-color: rgba(0, 0, 0, 0);
	// transition: ease-in-out 350ms;
	// transition-property: background-color, color, border, opacity;

	&,
	&:active,
	&:focus,
	&:hover,
	&:visited {
		color: inherit;
		text-decoration: none;
		outline: none;
	}

	/*
	&:hover {
		box-shadow: 0 10px 10px -5px rgba(0, 0, 0, 0.15);
	}
	*/

	&[disabled] {
		opacity: 0.5;
		pointer-events: none;
	}
}

a,
button,
.btn {
	@extend %btn;
}

.btn--link {
	@extend %btn;
	display: inline-block;

	span {
		display: inline-block;
		padding: 4px;
		font-size: 16px;
		line-height: 1;
		font-weight: $font-semibold;
		border: none;
		transition: all ease-in-out .2s;
	}

	&,
	&:active,
	&:visited {
		span {
			color: $foreground;
			transition: all ease-in-out .2s;
		}
	}

	&:focus,
	&:hover {
		span {
			background: $background-secondary;
			color: $foreground;
		}
	}

	@include media-breakpoint-down(xs) {
		display: block;
	}
}

.btn--category {
	@extend %btn;
	display: inline-block;

	span {
		display: inline-block;
		padding: 8px 16px;
		font-size: 16px;
		line-height: 1;
		font-weight: $font-semibold;
		border: none;
		transition: all ease-in-out .2s;
	}

	&,
	&:active,
	&:visited {
		span {
			background: $background-secondary;
			color: $foreground;
			transition: all ease-in-out .2s;
		}
	}

	&:focus,
	&:hover {
		span {
			background: $background-negative;
			color: $foreground-negative;
		}
	}

	.section--negative & {

		&,
		&:active,
		&:focus,
		&:hover,
		&:visited {
			span {
				background: $background-negative;
				color: $foreground-negative;
			}
		}
	}

	@include media-breakpoint-down(xs) {
		display: block;
	}
}

.btn--zoom {
	.icon--search {
		width: 20px;
		height: 20px;
	}

	.icon--close {
		width: 20px;
		height: 20px;
	}
}

.btn--wishlist {
	.icon--wishlist {
		width: 25px;
		height: 22px;
	}

	.icon--wishlist+span {
		margin-left: 10px;
	}
}
