html {
	width: 100%;
	height: 100%;
	margin: 0;
	overflow-x: hidden;
	overscroll-behavior: none;
	scroll-behavior: smooth;
}

body {
	width: 100%;
	min-height: 100%;
	margin: 0;
	padding: 0;

	@include scrollbar();

}

[unselectable] {
	-webkit-touch-callout: none;
	user-select: none;
}

[if],
[for] {
	display: none;
}
